import {
		mapState,  
	    mapMutations  
	} from 'vuex'; 
export default {
	data() {
		return {
			sendData: {
			    content: "",
			    nickname: ""
			},
			messageList: [],
		}
	},
	onLoad() {

	},
	onShow() {
		var that = this;
		that.checkLocalInfo();
		var that = this;
		if(that.token !== null){
			uni.request({
				url: this.global_baseUrl + '/messageBoard/',
				data: {
					
				},
				header: {
					'Authorization': 'Token  ' + this.token,
				},
				success: (res) => {
					this.messageList = res.data;
				}
			});
		}
	},
	computed: {
		...mapState(['token', 'globalUserInfo'])
	},
	methods: {
		checkLocalInfo() {
			var that = this;
			if(that.token == null){
				setTimeout(() => {
					uni.showModal({
						title: '提示',
						content: '您还未登录，登录体验更多功能',
						cancelText: '先逛逛',
						confirmText: '去登录',
						success: function(res) {
							if (res.confirm) {
								uni.navigateTo({
									url: '/pages/login/login'
								});
							} else if (res.cancel) {}
						}
					});
				}, 800)
				uni.hideLoading();
			} else {
				uni.hideLoading();
			}
		},
		// inputContent(e) {
		// 	this.content = e.target.value
		// },
		// inputNickname(e) {
		// 	this.nickname = e.target.value
		// },
		submit() {
			if (this.sendData.content.length==0){
				uni.showToast({
					title: "请输入留言内容",
					icon: 'none'
				})
				return false;
			}
			
			if (this.sendData.content.length > 80){
				uni.showToast({
					title: "留言内容不能超过80字",
					icon: 'none'
				})
				return false;
			}
			
			if (this.sendData.nickname.length==0){
				uni.showToast({
					title: "请输入昵称",
					icon: 'none'
				})
				return false;
			}
			if (this.sendData.nickname.length > 11){
				uni.showToast({
					title: "昵称长度不能超过11字",
					icon: 'none'
				})
				return false;
			}
			
			var that = this;
			uni.showLoading({
			    title: '提交中'
			});
			uni.request({
			    url: that.global_baseUrl + '/messageBoard/',
			    data: {
			        content: this.sendData.content,
					nickname: this.sendData.nickname,
			    },
				method:"POST",
			    header: {
			        'Content-Type': 'application/x-www-form-urlencoded',
					'Authorization': 'Token  ' + this.token,
			    },
			    success: (res) => {
					if (res.data == "ok"){
						uni.showToast({
							title: "提交成功!",
							icon: 'success'
						})
						uni.request({
							url: this.global_baseUrl + '/messageBoard/',
							data: {
								
							},
							header: {
								'Authorization': 'Token  ' + this.token,
							},
							success: (res) => {
								this.messageList = res.data;
							}
						});
					}
			    },
			});
			this.sendData.content="";
			this.sendData.nickname="";
		},
	}
}
